@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Montserrat', sans-serif;
}
.banner{
  width: 100%;
  height: 100vh;
  background: url(/Food-web/static/media/bg.38f4b351.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
}
.banner_content{
  display: flex;
  align-items: center;
  height: 100vh;
  color: #fff;
}
.container{
  width: 1200px;
  max-width: 1200px;
  margin: 0 auto;
}
.banner-text{
  width:500px;
}
.banner-text h3{
  font-size: 50px;
}
.banner-text h1{
  font-size: 80px;
  line-height:80px;
  font-weight: 900;
  margin-top: 20px;
}
.banner-text p{
  font-size: 25px;
  font-weight: 400;
  margin-top: 25px;
}
.navbar{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;

}
.navbar_text{
  display: flex;
  height: 60px;
  align-items: center;
  width: 1200px;
  margin: 0 auto;
}
.logo{
  width: 40%;
}
.logo img{
  width: 220px;
}
.navbar-ul{
  width: 60%;
  list-style: none;
  display: flex;
  justify-content: flex-end;
}
.navbar-ul li a{
  text-decoration: none;
  color: #fff;
  padding: 0 15px;
  font-weight: bold;
  text-transform: uppercase;
}
.banner-btn{
  margin-top: 40px;
}
.btn{
  text-decoration: none;
  border: none;
  outline: none;
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 30px;
  font-size: 12px;
  padding: 20px 40px;


}
.btn-smart{
  background: rgb(187, 24, 152);
}
.about{
  padding: 80px 0;
}
.row{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.col-6{
  width: 50%;
}
.p-25{
  padding: 45px;
}
.about h3{
  color: rgb(37,37,37,0.7);
  font-size: 30px;
  margin-bottom: 12px;
  font-weight: 700;
}
.about h1{
  font-size: 40px;
  color: #252525;
  font-weight: 700;
  line-height: 40px;
  margin-bottom: 15px;
}
.about p{
  font-size: 18px;
  color: #8b8b8b;
  font-weight: 300;
}
.about-btn{
  margin-top: 35px;
}
.about-img{
  width: 100%;
  overflow: hidden;
}
.about-img img{
  width: 100%;
}
.shop{
  width: 100%;
  background: url(/Food-web/static/media/bg.38f4b351.jpg);
  height: 450px;
  background-attachment: fixed;
}
.shop-text{
  display: flex;
  height: 450px;
  align-items: center;
  width: 400px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
color: white;
}
.shop-text h1{
  font-size: 40px;
  line-height: 60px;
  font-weight: 600;
  font-style: normal;
  letter-spacing: 0.5px;
}
.shop-btn{
  margin-top: 35px;
}
.clients {
  width: 100%;
  padding: 100px 0;
  background: url(/Food-web/static/media/bg.38f4b351.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  background-attachment: fixed;
}
.col-3{
  width: 25%;
}
.client {
  text-align: center;
}
.client h1 {
  font-style: 45px;
}
.client p {
  font-style: 18px;
  font-weight: 400;
  margin-top: 10px;
}
.prices {
  width: 100%;
  padding: 80px 0;
}

.price {
  padding: 20px;
  text-align: center;
}
.price__img {
  width: 100%;
}

.price__img img {
  width: 250px;
}

.price__heading {
  margin-top: 20px;
  font-size: 25px;
}
.col-4{
  width: 33.33333333333333%;
}
.price__text {
  margin-top: 15px;
  color: #888888;
  font-size: 18px;
  font-weight: 300;
}

.price_rs {
  font-size: 30px;
  margin-top: 15px;
}
@media (max-width: 700px) {
  .navbar_text{
    display: flex;
    height: auto;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0 auto;
  }
  .logo{
    margin-bottom: 20px;
    margin-top: 20px;
  }
  .container {
    width: 100%;
    padding: 0 30px;
  }
  .banner-text {
    width: auto;
  }
  .banner-text h3 {
    font-size: 25px !important;
  }
  .banner-text h1 {
    font-size: 40px;
    line-height: 60px;
    font-weight: 900;
    margin-top: 20px;
  }
  .banner-text p {
    font-size: 20px;
    font-weight: 400;
    margin-top: 20px;
  }
  .navbar-ul {
    width: 100%;
    justify-content: center;
  }
  .navbar-ul li a {
    text-decoration: none;
    color: #fff;
    padding: 0 10px;
    font-weight: 300;
    text-transform: uppercase;
  }
  .col-3,
  .col-4,
  .col-6 {
    width: 100%;
  }
  .client {
    margin: 20px 0;
  }
  .about h1 {
    font-size: 25px;
  }
  .shop h1 {
    font-size: 25px;
  }
  .menu h1 {
    font-size: 25px;
  }
}
